.search{
  padding: 38px 10px 49px 10px;
  text-align: center;
  &__title{
    margin: 0 0 25px 0;
    text-transform: uppercase;
    font-size: 21px;
    line-height: 1.24;
    font-weight: 700;
  }
  &__text{
    margin: 0 0 32px 0;
    text-transform: uppercase;
    font-size: 14px;
    line-height: 1.5;
    color: #333333;
  }
  &__link{
    text-decoration: none;
    width: 100%;
    max-width: 284px;
    margin: 0 auto;
    padding: 16px 0;
  }
}
