.header{
  width: 100%;
  height: 56px;
  position: relative;
  &__logo{
    display: block;
    position: absolute;
    top: 0;
    left: 50%;
    z-index: 1;
    transform: translate(-50%, 0);
  }
  &__button{
    position: absolute;
    top: 0;
    right: 0;
    z-index: 3;
    width: 56px;
    height: 56px;
    background: url("../../img/burger-button.svg") center no-repeat;
    cursor: pointer;
  }
  &__button--toggle{
    position: absolute;
    top: 0;
    right: 0;
    z-index: 3;
    width: 56px;
    height: 56px;
    background: url("../../img/icon-cross.svg") center no-repeat;
    cursor: pointer;
  }
}
